import { useEffect, useState } from "react"
import { useParams } from "next/navigation"

interface Product {
  _id: string
  name: string
  price: number
  image: string
  description: string
}

export default function SubcategoryPage() {
  const { category, subcategory } = useParams() // Get category & subcategory from URL
  const [products, setProducts] = useState<Product[]>([])
  const [loading, setLoading] = useState(true)

  useEffect(() => {
    const fetchProducts = async () => {
      try {
        const res = await fetch(`http://localhost:5000/api/products/${category}/${subcategory}`)
        const data = await res.json()
        setProducts(data)
      } catch (error) {
        console.error("Error fetching subcategory products:", error)
      } finally {
        setLoading(false)
      }
    }

    fetchProducts()
  }, [category, subcategory])

  if (loading) return <p className="text-center text-lg">Loading products...</p>

  return (
    <div className="container mx-auto p-4">
      <h1 className="text-3xl font-bold mb-6 text-center capitalize">
        {subcategory} Products
      </h1>

      <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-6">
        {products.length > 0 ? (
          products.map((product) => (
            <div key={product._id} className="border rounded-lg p-4 shadow-md hover:shadow-lg transition">
              <img src={product.image} alt={product.name} className="w-full h-52 object-cover rounded-lg mb-4" />
              <h2 className="text-xl font-semibold">{product.name}</h2>
              <p className="text-gray-600">{product.description}</p>
              <p className="text-lg font-bold mt-2">${product.price}</p>
            </div>
          ))
        ) : (
          <p className="text-center col-span-full text-gray-500">No products found in this subcategory.</p>
        )}
      </div>
    </div>
  )
}
